<template lang="pug">
#FacilityInfo(v-if="facility")
    h3 {{ facility.properties.FacilityName }}
    table
        tr
            th Sports
            td {{ p.SportsPlayed }}
        tr
            th Remoteness
            td {{ remotenessArea }}
        tr
            th LGA
            td {{ lga }}
</template>

<script>
// import queryLngLat from 'query-remote-tiles';
const queryLngLat = require('query-remote-tiles').queryLngLat;
export default {
    name: "FacilityInfo",
    data: () => ({
        facility: undefined,
        remotenessArea: undefined,
        lga: undefined
    }),
    created() {
        window.FacilityInfo = this;
    }, watch: {
        facility() {
            queryLngLat('https://vector-tiles.terria.io/FID_RA_2011_AUST/{z}/{x}/{y}.pbf', this.facility.geometry.coordinates, 11, 'FID_RA_2011_AUST')
            .then(ra => this.remotenessArea = ra.features[0].properties.RA_NAME11);

queryLngLat('https://vector-tiles.terria.io/FID_LGA_2011_AUST/{z}/{x}/{y}.pbf', this.facility.geometry.coordinates, 11, 'FID_LGA_2011_AUST')
            .then(lga => this.lga = lga.features[0].properties.LGA_NAME11);
        }
    }, computed: {
        p() {
            return this.facility.properties;
        }
    }
}
</script>

<style scoped>
#FacilityInfo {
    margin: 1em 0;
    border-top: 2px solid lightblue;
    border-bottom: 2px solid lightblue;
    background: hsl(180, 80%, 95%);
    min-height: 100px;
    padding: 1em 0.5em;
}

table {
    text-align: left;
}

</style>